import React, { useEffect } from 'react';
import Helmate from 'react-helmet';
import './css/home_style.css';
import Aos from 'aos';
import 'aos/dist/aos.css';
import { Link } from 'react-router-dom';

function Home(){
    useEffect(() => {
        Aos.init({duration:3000});
    });
    return(
        <div>
            <Helmate>
                <title>Home|Matrix Calculator</title>
            </Helmate>
            <div>
                <h1 className="naming">Matrix Calculator</h1>
                <img className="intro_matrix" alt="Matris" src="https://upload.wikimedia.org/wikipedia/commons/b/bf/Matris.png"></img>
                <h2 className="slogan" id="independentHeader">"Math means <br></br>&emsp;&ensp; Independence"</h2>
            </div>
            <div className="about">
                <p>
                    Are you facing problems to solving matrices related maths with your 100ms???
                </p>
                <p>
                    You are in right Place....
                </p>
            </div>
            <div className="cart">
                <div data-aos="fade-right" className="cards">
                    <p><Link to="/matrix_calculator/addition">Addition</Link></p>
                </div>
                <div data-aos="fade-left" className="cards">
                    <p><Link to="/matrix_calculator/subtraction">Subtraction</Link></p>
                </div>
                <div data-aos="fade-right" className="cards">
                    <p><Link to="/matrix_calculator/multiplication">Multiplication</Link></p>
                </div>
                <div data-aos="fade-left" className="cards">
                    <p><Link to="/matrix_calculator/transposition">Transposition</Link></p>
                </div>
                <div data-aos="fade-right" className="cards">
                    <p><Link to="/matrix_calculator/determination">Determination</Link></p>
                </div>
            </div>
            <div className="startingAnchor">
                <a href="/matrix_calculator/addition">Let's Start</a>
            </div>
        </div>
    )
};


export default Home;
