import {React, Component} from 'react';
import { Helmet } from 'react-helmet';
import FunctionalityNav from './FunctionalityNav';


export default class Multiplication extends Component {
    constructor(){
        super();
        this.state={
            rowA:0,
            columnA:0,
            rowB:0,
            columnB:0,
            clicked_value:false,
        };        
    }


    onChangeHandler=(event)=>{
        var myName = event.target.name;
        var myValue = event.target.value;
        this.setState({[myName]:myValue});
    };


    inputTakerA=(event)=>{
        var entityA = event.target.name;
        var entityValueA = event.target.value;
        this.setState({[entityA]:entityValueA})
    }


    inputTakerB=(event)=>{
        var entityB = event.target.name;
        var entityValueB = event.target.value;
        this.setState({[entityB]:entityValueB});
    }


    result=()=>{
        document.getElementById("resultDOM").innerHTML = '';
        this.setState({clicked_value:true});
        const x = new Array(this.state.rowA);  // initialize array of rows
        for (var r = 0; r < this.state.rowA; ++r) {
            x[r] = new Array(this.state.columnB); // initialize the current row
            for (var c = 0; c < this.state.columnB; ++c) {
                x[r][c] = 0;             // initialize the current cell
                for (var k = 0; k < this.state.columnA; ++k) {
                    {/*m[r][c] += a[r][i] * b[i][c];*/}
                    var reduceA = "i" + r + "j" + k;
                    var reduceB = "m" + k + "n" + c;
                    x[r][c] += parseInt(this.state[reduceA]) * parseInt(this.state[reduceB]);
                }
            }
        }
        for (var xx = 0; xx < x.length; ++xx) {
            document.getElementById("resultDOM").innerHTML += '&nbsp;&nbsp;'+'  ' + x[xx].join(' ')+'<br>';
          }
        return x;
    }


    render(){
        const tableA = [];
        for (var i = 0;i < this.state.rowA; i++){
            tableA.push(<br></br>);
            for(var j = 0; j < this.state.columnA; j++){
                var inputNameA = "i" + i + "j" + j;
                tableA.push(<input name={inputNameA} onChange={this.inputTakerA} type="number"></input>);
            }
        }


        const tableB = [];
        for (var m = 0;m < this.state.rowB; m++){
            tableB.push(<br></br>);
            for(var n = 0; n < this.state.columnB; n++){
                var inputNameB = "m" + m + "n" + n;
                tableB.push(<input name={inputNameB} onChange={this.inputTakerB} type="number"></input>);
            }
        }


        if(this.state.rowA != 0 && this.state.rowB != 0 && this.state.columnA != 0 && this.state.columnB != 0){
            return(
                <div className="functionality_mainDiv">
                    <Helmet>
                        <title>Multiplication</title>
                    </Helmet>
                    <h1 className="functionalityH1">Multiplying two matrices</h1>
                    <div>
                        <span className="orderQuery">Enter the order of first Matrix:</span><input name="rowA" type="number" onChange={this.onChangeHandler}></input><b className="boldedQueryLevel">x</b><input name="columnA" type="number" onChange={this.onChangeHandler}></input><br></br>
    
                        <span className="orderQuery">Enter the order of second Matrix:</span><input name="rowB" type="number" onChange={this.onChangeHandler}></input><b className="boldedQueryLevel">x</b><input name="columnB" type="number" onChange={this.onChangeHandler}></input><br></br>
                    
    
                        <h3 className="matrix_notation">Matrix A</h3>
    
                        <div className="tableDiv">
                            {tableA}
                        </div>
    
                        <br></br><br></br>
                        <h3 className="matrix_notation">Matrix B</h3>
    
                        <div className="tableDiv">
                            {tableB}
                        </div>
    
    
                        <div className="resultDiv">
                            <button onClick={this.result}>Click Me</button><br></br>
                            <h3 className="matrix_notation">Result(A * B):</h3>
                            {/*{result}*/}
                            
                            <span className="resulta" id="resultDOM"></span>
                        </div>
                        
    
                    </div>
                    <FunctionalityNav></FunctionalityNav>
                </div>
            )
        }
        else{
            return(
                <div className="functionality_mainDiv">
                    <Helmet>
                        <title>Multiplication</title>
                    </Helmet>
                    <h1 className="functionalityH1">Multiplying two matrices</h1>
                    <div>
                        <span className="orderQuery">Enter the order of first Matrix:</span><input name="rowA" type="number" onChange={this.onChangeHandler}></input><b className="boldedQueryLevel">x</b><input name="columnA" type="number" onChange={this.onChangeHandler}></input><br></br>
    
                        <span className="orderQuery">Enter the order of second Matrix:</span><input name="rowB" type="number" onChange={this.onChangeHandler}></input><b className="boldedQueryLevel">x</b><input name="columnB" type="number" onChange={this.onChangeHandler}></input><br></br>
                    </div>
                    <FunctionalityNav></FunctionalityNav>
                </div>
            )
        }
    };
};