import logo from './logo.svg';
import './App.css';
import {BrowserRouter, Switch, Route} from 'react-router-dom';
import Home from './components/Home';
import Multiplication from './components/Multiplication';
import Addition from './components/Addition';
import Subtraction from './components/Subtraction';
import Transposition from './components/Transposition';
import Determination from './components/Determination';

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Switch>
          <Route exact path="/matrix_calculator/" component={Home}></Route>
          <Route exact path="/matrix_calculator/addition" component={Addition}></Route>
          <Route exact path="/matrix_calculator/subtraction" component={Subtraction}></Route>
          <Route exact path="/matrix_calculator/multiplication" component={Multiplication}></Route>
          <Route exact path="/matrix_calculator/transposition" component={Transposition}></Route>
          <Route exact path="/matrix_calculator/determination" component={Determination}></Route>
        </Switch>
      </BrowserRouter>
    </div>
  );
}

export default App;
