import {React, Component} from 'react';
import {Helmet} from 'react-helmet';
import FunctionalityNav from './FunctionalityNav';


export default class Determination extends Component {
    constructor(){
        super();
        this.state={
            row:0,
            column:0,
            determinant:'',
        };        
    }


    onChangeHandler=(event)=>{
        var myName = event.target.name;
        var myValue = event.target.value;
        this.setState({'column':myValue});
        this.setState({[myName]:myValue});
    };


    inputTaker=(event)=>{
        var entity = event.target.name;
        var entityValue = event.target.value;
        this.setState({[entity]:entityValue})
    };


    process=(matrix)=>{
        if(matrix.length == 2){
            var det = (matrix[0][0] * matrix[1][1]) - (matrix[0][1] * matrix[1][0]);
            return det;
        }
        else{
            console.log(matrix)
            var ii = matrix[0][0] * this.process([[matrix[1][1],matrix[1][2]],[matrix[2][1],matrix[2][2]]])
            var jj = matrix[0][1] * this.process([[matrix[1][0],matrix[1][2]],[matrix[2][0],matrix[2][2]]])
            var kk = matrix[0][2] * this.process([[matrix[1][0],matrix[1][1]],[matrix[2][0],matrix[2][1]]])
            return ii - jj + kk;
        }
    };

    result=()=>{
        var arr = [];
        for(var i = 0; i < this.state.row; i++){
            arr[i] = new Array();
        }
        for(var a=0;a < this.state.row;a++){
            for(var b=0;b < this.state.row;b++){
                var entity = "i" + a + "j" + b;
                var entityValue = this.state[entity];
                arr[a][b] = entityValue;
            }
        }
        var x = this.process(arr);
        this.setState({determinant:x})
    }



    render(){
        const tableA = [];
        for (var i = 0;i < this.state.row; i++){
            tableA.push(<br></br>);
            for(var j = 0; j < this.state.column; j++){
                var inputName = "i" + i + "j" + j;
                tableA.push(<input name={inputName} onChange={this.inputTaker} type="number"></input>);
            }
        }


        return(
            <div className="functionality_mainDiv">
                <Helmet>
                    <title>Determination</title>
                </Helmet>
                <h1 className="functionalityH1">Calculate the determinant</h1>
                <div>
                    <span className="orderQuery">Enter Dimension:</span>
                    <select style={{widows:'32px',fontSize:'32px'}} name="row" onChange={this.onChangeHandler}>
                        <option></option>
                        <option value="2">2</option>
                        <option value="3">3</option>
                    </select>
                    <br></br>
                

                    <div className="tableDiv">
                        {tableA}
                    </div>

                    <br></br><br></br>

                    <button onClick={this.result}> Show Result</button>
                    <h3 style={{fontSize:'36px'}} className="matrix_notation">{this.state.determinant}</h3>
                    

                </div>
                <FunctionalityNav></FunctionalityNav>
            </div>
        )
    };
};

