import {React,Component} from 'react';
import { Helmet } from 'react-helmet';
import FunctionalityNav from './FunctionalityNav';


class Transposition extends Component{
    constructor(){
        super();
        this.state={
            row:0,
            column:0,
        };        
    }


    onChangeHandler=(event)=>{
        var myName = event.target.name;
        var myValue = event.target.value;
        this.setState({[myName]:myValue});
    };


    inputTaker=(event)=>{
        var entity = event.target.name;
        var entityValue = event.target.value;
        this.setState({[entity]:entityValue})
    }

    
    render(){
        const table = [];
        for (var i = 0;i < this.state.row; i++){
            table.push(<br></br>);
            for(var j = 0; j < this.state.column; j++){
                var inputName = "i" + i + "j" + j;
                table.push(<input name={inputName} onChange={this.inputTaker} type="number"></input>);
            }
        }


        const result = [];
        for(var e = 0; e < this.state.row; e++){
            result.push(<br></br>)
            for(var f = 0; f < this.state.column; f++){
                var reduce = "i" + f + "j" + e;
                result.push(<input readOnly value={this.state[reduce]} type="number"></input>);
            }
        }
        if(this.state.row != 0 && this.state.column != 0){
            return(
                <div className="functionality_mainDiv">
                    <Helmet>
                        <title>Transposition</title>
                    </Helmet>
                    <h1 className="functionalityH1">Transpose the matrix</h1>
                    <div>
                        <span className="orderQuery">Enter the order of Matrices:</span><input name="row" type="number" onChange={this.onChangeHandler}></input><b className="boldedQueryLevel">x</b><input name="column" type="number" onChange={this.onChangeHandler}></input><br></br>
                    
    
                        <h3 className="matrix_notation">Matrix A</h3>
    
                        <div className="tableDiv">
                            {table}
                        </div>
    
                        <br></br><br></br>
    
    
                        <div className="resultDiv">
                            <h3 className="matrix_notation">Transpose of A is..</h3>
                            {result}
                        </div>
                    </div>
                    <FunctionalityNav></FunctionalityNav>
                </div>
            )
        }
        else{
            return(
                <div className="functionality_mainDiv">
                    <Helmet>
                        <title>Transposition</title>
                    </Helmet>
                    <h1 className="functionalityH1">Transpose the matrix</h1>
                    <div>
                        <span className="orderQuery">Enter the order of Matrices:</span><input name="row" type="number" onChange={this.onChangeHandler}></input><b className="boldedQueryLevel">x</b><input name="column" type="number" onChange={this.onChangeHandler}></input><br></br>
                    </div>
                    <FunctionalityNav></FunctionalityNav>
                </div>
            )
        }
    };
}


export default Transposition;
