import React from 'react';
import { Link,NavLink } from 'react-router-dom';


function FunctionalityNav(){
    return(
        <div className="functionalityNav">
            <div className="functionalityCards">
                <p><NavLink exact activeStyle={{display:'none'}} to="/matrix_calculator/addition">Addition</NavLink></p>
            </div> 
            <div className="functionalityCards">
                <p><NavLink exact activeStyle={{display:'none'}} to="/matrix_calculator/subtraction">Subtraction</NavLink></p>
            </div>
            <div className="functionalityCards">
                <p><NavLink exact activeStyle={{display:'none'}} to="/matrix_calculator/multiplication">Multiplication</NavLink></p>
            </div>
            <div className="functionalityCards">
                <p><NavLink exact activeStyle={{display:'none'}} to="/matrix_calculator/transposition">Transposition</NavLink></p>
            </div>
            <div className="functionalityCards">
                <p><NavLink exact activeStyle={{display:'none'}} to="/matrix_calculator/determination">Determination</NavLink></p>
            </div>
        </div>
    );
}
export default FunctionalityNav;
