import {React,Component} from 'react';
import { Helmet } from 'react-helmet';
import FunctionalityNav from './FunctionalityNav';


class Subtraction extends Component{
    constructor(){
        super();
        this.state={
            row:0,
            column:0,
        };        
    }


    onChangeHandler=(event)=>{
        var myName = event.target.name;
        var myValue = event.target.value;
        this.setState({[myName]:myValue});
    };


    inputTakerA=(event)=>{
        var entityA = event.target.name;
        var entityValueA = event.target.value;
        this.setState({[entityA]:entityValueA})
    }


    inputTakerB=(event)=>{
        var entityB = event.target.name;
        var entityValueB = event.target.value;
        this.setState({[entityB]:entityValueB});
    }

    
    render(){
        const tableA = [];
        for (var i = 0;i < this.state.row; i++){
            tableA.push(<br></br>);
            for(var j = 0; j < this.state.column; j++){
                var inputNameA = "i" + i + "j" + j;
                tableA.push(<input name={inputNameA} onChange={this.inputTakerA} type="number"></input>);
            }
        }


        const tableB = [];
        for(var m = 0;m < this.state.row; m++){
            tableB.push(<br></br>);
            for (var n = 0; n < this.state.column; n++) {
                var inputNameB = "x" + m + "y" + n;
                tableB.push(<input name={inputNameB} onChange={this.inputTakerB} type="number"></input>);
            }   
        }


        const result = [];
        for(var e = 0; e < this.state.row; e++){
            result.push(<br></br>)
            for(var f = 0; f < this.state.column; f++){
                var reduceA = "i" + e + "j" + f;
                var reduceB = "x" + e + "y" + f;
                result.push(<input readOnly value={parseInt(this.state[reduceA]) - parseInt(this.state[reduceB])} type="number"></input>);
            }
        }
        if(this.state.row != 0 && this.state.column != 0){
            return(
                <div className="functionality_mainDiv">
                    <Helmet>
                        <title>Subtraction</title>
                    </Helmet>
                    <h1 className="functionalityH1">Subtracting two matrices</h1>
                    <div>
                        <span className="orderQuery">Enter the order of Matrices:</span><input name="row" type="number" onChange={this.onChangeHandler}></input><b className="boldedQueryLevel">x</b><input name="column" type="number" onChange={this.onChangeHandler}></input><br></br>
                    
    
                        <h3 className="matrix_notation">Matrix A</h3>
    
                        <div className="tableDiv">
                            {tableA}
                        </div>
    
                        <br></br><br></br>
                        <h3 className="matrix_notation">Matrix B</h3>
    
                        <div className="tableDiv">
                            {tableB}
                        </div>
    
    
                        <div className="resultDiv">
                            <h3 className="matrix_notation">Result(A - B):</h3>
                            {result}
                        </div>
                        
    
                    </div>
                    <FunctionalityNav></FunctionalityNav>
                </div>
            )
        }
        else{
            return(
                <div className="functionality_mainDiv">
                    <Helmet>
                        <title>Subtraction</title>
                    </Helmet>
                    <h1 className="functionalityH1">Subtracting two matrices</h1>
                    <div>
                        <span className="orderQuery">Enter the order of Matrices:</span><input name="row" type="number" onChange={this.onChangeHandler}></input><b className="boldedQueryLevel">x</b><input name="column" type="number" onChange={this.onChangeHandler}></input><br></br>
                    </div>
                    <FunctionalityNav></FunctionalityNav>
                </div>
            )
         
        }
    };
}


export default Subtraction;
